/**
 * ProfileForm.js
 * The user detail form in profile page
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';
import DropDownV2 from '../Forms/DropDown/DropDownV2';
import TextInput from '../Forms/TextInput/TextInput';

import { getPostal } from '../../actions/insuranceActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { saveProfile } from '../../actions/userActions';
import { validateForm, dateReValidate } from '../../lib/validators';

import './Profile.scss';

const userLabels = [
  'firstName',
  'surname',
  'id_type',
  'nric',
  'nationality',
  'dateOfBirth',
  'gender',
  'marital_status',
  'race',
  'occupation',
  'contact',
  'smoker',
  'blk_no',
  'address',
  'unit_no',
  'postal_code',
];

const profileFormName = 'profile';

class ProfileForm extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { userDetails, change } = nextProps;

    let result = null;
    if (userDetails) {
      if (!prevState.userDetails || userDetails.data !== prevState.userDetails.data) {
        ProfileForm.syncStoreToForm(userDetails.data, change);
        result = { userDetails };
      }
    }

    return result;
  }

  static syncStoreToForm(userDetails, change) {
    _.map(userLabels, (label) => {
      change(label, userDetails[label]);
    });
  }

  constructor(props) {
    super(props);

    this.state = {
      errorStringData: {},
      message: null,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  async validateForm(e, isSubmit = false) {
    const errorResult = validateForm(e, userLabels);

    if (isSubmit) {
      const postalResult = await getPostal(e.postal_code);
      if (!postalResult) {
        errorResult.postal_code = 'Invalid postal code';
      }
    }

    this.setState({
      errorStringData: errorResult,
    });

    return errorResult;
  }

  async handleSubmit(e, isFirstValidate = true) {
    const {
      userDetails,
      loading,
      loadingEnd,
      storeForm,
    } = this.props;
    const formToUse = isFirstValidate ? e : storeForm;
    const errorResult = await this.validateForm(formToUse, true);
    const isEmptyResult = _.isEmpty(errorResult);

    setTimeout(this.props.updateTabHeight, 10);
    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      errorResult,
      error => this.setState({ errorStringData: error }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (isEmptyResult &&
      userDetails &&
      userDetails.data &&
      userDetails.data.token) {
      loading();
      const result = await this.props.saveProfile(userDetails.data.token, formToUse);
      const message = result ? 'Profile has been updated.' : 'An error has occured. Please try again.';
      this.setState({
        message,
      });

      loadingEnd();
    }
  }

  renderMessage() {
    const { message } = this.state;
    if (message) {
      return (
        <Col xs={12}>
          <p className="message">{message}</p>
        </Col>
      );
    }

    return null;
  }

  render() {
    const { errorStringData } = this.state;
    const {
      change,
      form,
      handleSubmit,
      constants,
      storeForm,
    } = this.props;

    return (
      <form onSubmit={handleSubmit(this.handleSubmit)}>
        <Row className="profile-form" noGutters>
          <Col className="form-right-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name="firstName"
              placeholderString=""
              size="medium"
              zIndex={99}
              desc="First name"
              errorString={errorStringData.firstName}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name="surname"
              placeholderString=""
              size="medium"
              zIndex={97}
              desc="Last name"
              errorString={errorStringData.surname}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-right-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="id_type"
              placeholderString="Select ID Type"
              dropList={constants.idTypes}
              size="medium"
              zIndex={96}
              desc="Type of ID"
              errorString={errorStringData.id_type}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name="nric"
              placeholderString=""
              size="medium"
              zIndex={95}
              desc="ID Number"
              errorString={errorStringData.nric}
              validateForm={() => this.validateForm(storeForm)}
              toUpper
            />
          </Col>
          <Col className="form-right-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="nationality"
              placeholderString="Select Nationality"
              dropList={constants.nationalities}
              size="medium"
              zIndex={94}
              desc="Nationality"
              errorString={errorStringData.nationality}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <DatePickerV2
              form={form}
              change={change}
              name="dateOfBirth"
              size="medium"
              zIndex={93}
              desc="Date of birth"
              errorString={errorStringData.dateOfBirth}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-right-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="gender"
              placeholderString="Select Gender"
              dropList={constants.gender}
              size="medium"
              zIndex={92}
              desc="Gender"
              errorString={errorStringData.gender}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="marital_status"
              placeholderString="Select Marital Status"
              dropList={constants.maritalStatus}
              size="medium"
              zIndex={91}
              desc="Marital Status"
              errorString={errorStringData.marital_status}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-right-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="race"
              placeholderString="Select Race"
              dropList={constants.race}
              size="medium"
              zIndex={90}
              desc="Race"
              errorString={errorStringData.race}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="occupation"
              placeholderString="Select Occupation"
              dropList={constants.occupations}
              size="medium"
              zIndex={89}
              desc="Occupation"
              errorString={errorStringData.occupation}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-right-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name="contact"
              placeholderString=""
              size="medium"
              zIndex={88}
              desc="Mobile Number"
              errorString={errorStringData.contact}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name="smoker"
              placeholderString="Select Smoking Status"
              dropList={constants.smoker}
              size="medium"
              zIndex={87}
              desc="Smoking Status"
              errorString={errorStringData.smoker}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-seperator" xs={12}>
            <p>Address</p>
          </Col>
          <Col className="form-right-padding" xs={6}>
            <TextInput
              form={form}
              change={change}
              name="blk_no"
              placeholderString=""
              size="medium"
              zIndex={86}
              desc="Block No"
              errorString={errorStringData.blk_no}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={6}>
            <TextInput
              form={form}
              change={change}
              name="address"
              placeholderString=""
              size="medium"
              zIndex={86}
              desc="Street"
              errorString={errorStringData.street}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-right-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name="unit_no"
              placeholderString=""
              size="medium"
              zIndex={85}
              desc="Unit No"
              errorString={errorStringData.unit_no}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="form-left-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name="postal_code"
              placeholderString=""
              size="medium"
              zIndex={84}
              desc="Postcode"
              errorString={errorStringData.postal_code}
              validateForm={() => this.validateForm(storeForm)}
              isPostCode
              addressFields={{
                address: 'address',
                unitNo: 'unit_no',
              }}
            />
          </Col>
          {this.renderMessage()}
          <Col className="submit-container" xs={12}>
            <Button
              variant="medium-flat-green"
              width={200}
              height={72}
              type="submit"
            >
              <p>Save</p>
            </Button>
          </Col>
        </Row>
      </form>
    );
  }
}

ProfileForm.defaultProps = {
  storeForm: null,
};

ProfileForm.propTypes = {
  saveProfile: PropTypes.func.isRequired,
  change: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  constants: PropTypes.object.isRequired,
  userDetails: PropTypes.object.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  updateTabHeight: PropTypes.func.isRequired,
  storeForm: PropTypes.object,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(profileFormName)(state),
});

const mapDispatchToProps = dispatch => ({
  saveProfile: (token, profile) => (
    dispatch(saveProfile(token, profile))
  ),
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
});

export default reduxForm({
  form: profileFormName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(ProfileForm));

