/**
 * ProfileContainer.js
 * Profile Container.
 *
 * Will get cashback details only here.
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import PropTypes from 'prop-types';
import Profile from '../../components/Profile/Profile';

import { getRewardDetails, clearLoginData } from '../../actions/userActions';

class ProfileContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { userDetails, getRewards, history } = nextProps;

    let result = null;
    if (userDetails) {
      if (userDetails.data !== prevState.userDetails.data) {
        if (userDetails.data && userDetails.data.token) {
          getRewards(userDetails.data.token);
        } else {
          history.push('/');
        }

        result = { userDetails };
      }

      if (userDetails.data && userDetails.rewardData !== prevState.userDetails.rewardData) {
        result = { userDetails };
      }
    }

    return result;
  }

  constructor(props) {
    super(props);

    const { userDetails, getRewards } = this.props;
    if (userDetails && userDetails.data && !userDetails.rewardData) {
      getRewards(userDetails.data.token);
    }

    this.state = {
      userDetails,
    };

    this.logOut = this.logOut.bind(this);
  }

  logOut() {
    const { logout, history } = this.props;

    logout();
    history.push('/');
  }

  render() {
    const { userDetails } = this.state;
    const { constants } = this.props;
    const { data, rewardData } = userDetails;

    if (!userDetails.data || !constants) {
      return null;
    }

    return (
      <Profile
        userDetails={userDetails}
        rewardData={rewardData}
        validString={data.reward_points_expire_at}
        cashbackBalance={data.reward_points}
        logOut={this.logOut}
        constants={constants}
      />
    );
  }
}

ProfileContainer.defaultProps = {
  constants: null,
};

ProfileContainer.propTypes = {
  userDetails: PropTypes.object.isRequired,
  logout: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  constants: PropTypes.object,
  getRewards: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  userDetails: state.userDetails,
  constants: state.insurance.constants,
});

const mapDispatchToProps = dispatch => ({
  getRewards: token => (
    dispatch(getRewardDetails(token))
  ),
  logout: () => (
    dispatch(clearLoginData())
  ),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ProfileContainer));
