/**
 * ProfileCashback.js
 * Cashback portion for profile view
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Col, Table } from 'reactstrap';
import PropTypes from 'prop-types';

import CashbackLogo from 'assets/images/common/CashbackLogo.svg';
import { ISOStringToFormDate } from 'lib/dateFunctions';

import Button from 'components/FormsV3/Button/Button';

import './Profile.scss';

class ProfileCashback extends Component {
  constructor(props) {
    super(props);

    this.renderCashbackList = this.renderCashbackList.bind(this);
    this.renderFreshCashback = this.renderFreshCashback.bind(this);
  }

  renderCashbackList() {
    const { rewardData } = this.props;

    return _.map(rewardData, (data, index) => {
      const bgSyle = index % 2 ? 'cell-colored' : '';
      const date = ISOStringToFormDate(data.created_at);
      const pointsSymbol = data.type === 'add' ? '+' : '-';
      const pointsDeductStyle = data.type === 'add' ? '' : 'table-amount-minus';

      return (
        <Fragment key={`casback-${index}`}>
          <tr className="d-table-row d-sm-none resp-height">
            <td className="resp-height cell-header" colSpan={3}>
              <div className="table-item-container resp-height">
                <p className="table-item-text resp-text">{data.insurer} - {data.policy_name}</p>
              </div>
            </td>
          </tr>
          <tr>
            <td className={`d-none d-sm-table-cell ${bgSyle}`}>
              <div className="table-item-container">
                <p className="table-item-text">{data.insurer}</p>
                <p className="table-item-text">{data.policy_name}</p>
              </div>
            </td>
            <td>
              <div className={`table-item-container ${bgSyle}`}>
                <p className="table-item-text">{date}</p>
              </div>
            </td>
            <td>
              <div className={`table-item-container ${bgSyle}`}>
                <p className={`table-amount-text ${pointsDeductStyle}`}>{`${pointsSymbol}${data.reward_points}`}</p>
              </div>
            </td>
            <td>
              <div className={`table-item-container ${bgSyle}`}>
                <p
                  className={`table-completed-text ${data.status === 'pending' ? 'table-pending-text' : 'table-completed-text'}`}
                >
                  {data.status}
                </p>
              </div>
            </td>
          </tr>
        </Fragment>
      );
    });
  }

  renderFreshCashback() {
    const { history } = this.props;
    return (
      <Col className="fresh-cashback-container" xs={12}>
        <p className="fresh-text">No record found, start earning cashbacks by purchasing a Travel/Motor plan!</p>
        <Button
          variant="medium-flat-green"
          width={200}
          height={72}
          onClick={() => history.push('/categories')}
        >
          <p>Get Covered</p>
        </Button>
      </Col>
    );
  }

  render() {
    const { rewardData } = this.props;

    return (
      <Fragment>
        <Row>
          <Col className="cashback-history-container" xs={12}>
            <img src={CashbackLogo} alt="CashbackLogo.svg" />
            <p>Cashback History</p>
          </Col>
        </Row>
        <Row className="profile-cashback-container" noGutters>
          <Table responsive>
            <thead>
              <tr>
                <th className="d-none d-sm-table-cell">
                  <div className="table-item-container">
                    <p>Plan</p>
                  </div>
                </th>
                <th>
                  <div className="table-item-container">
                    <p>Date</p>
                  </div>
                </th>
                <th>
                  <div className="table-item-container">
                    <p>Amount</p>
                  </div>
                </th>
                <th>
                  <div className="table-item-container">
                    <p>Status</p>
                  </div>
                </th>
              </tr>
            </thead>
            {
              rewardData ?
                <tbody>
                  {this.renderCashbackList()}
                </tbody>
              :
                null
            }
          </Table>
          {
            !rewardData ?
              this.renderFreshCashback()
            :
              null
          }
        </Row>
      </Fragment>
    );
  }
}

ProfileCashback.defaultProps = {
  rewardData: null,
};

ProfileCashback.propTypes = {
  rewardData: PropTypes.array,
  history: PropTypes.object.isRequired,
};

export default withRouter(ProfileCashback);

