/**
 * Profile.js
 * Profile view for logged in personnal
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import { withRouter } from 'react-router-dom';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Female from 'assets/images/Profile/Female.png';
import Male from 'assets/images/Profile/Male.png';
import CashbackLogo from 'assets/images/common/CashbackLogo.svg';

import Button from 'components/FormsV3/Button/Button';
import CashbackBalance from '../Templates/CashbackBalance/CashbackBalance';
import TabView from '../Templates/TabView/TabView';

import FooterContainer from '../../containers/FooterContainer';
import ProfileCashback from './ProfileCashback';
import ProfileForm from './ProfileForm';

import './Profile.scss';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../actions/navActions';

class Profile extends Component {
  constructor(props) {
    super(props);

    this.renderLeftProfile = this.renderLeftProfile.bind(this);
    this.renderCashbackbalance = this.renderCashbackbalance.bind(this);
    this.renderCashbackInfo = this.renderCashbackInfo.bind(this);

    // Set Insurance Page Indicator
    const resetInsurance = onPageInsurance.NONE;
    // Reset More Page Indicator
    const resetMore = onPageMore.NONE;

    // Reset Business Page Indicator
    const resetBusiness = onPageBusiness.NONE;

    const {
      resetIndicatorInsurance,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);
  }

  renderLeftProfile() {
    const { logOut, userDetails, storeForm } = this.props;
    const profilePic = storeForm &&
      storeForm.gender &&
      storeForm.gender.toUpperCase() === 'FEMALE' ? Female : Male;

    return (
      <div className="profile-pic-container">
        <div className="profile-pic">
          <img src={profilePic} alt="Profile Pic" />
        </div>
        <p className="profile-name">{userDetails.data.surname} {userDetails.data.firstName}</p>
        <Button
          variant="medium-outline"
          width={132}
          height={36}
          onClick={logOut}
        >
          <p>Logout</p>
        </Button>
      </div>
    );
  }

  renderCashbackbalance() {
    const { cashbackBalance } = this.props;

    return (
      <CashbackBalance cashbackBalance={cashbackBalance.toString()} />
    );
  }

  renderCashbackInfo() {
    const { validString } = this.props;

    if (validString) {
      return (
        <div className="cashback-how">
          <p className="valid-text">Valid till: {validString}</p>
        </div>
      );
    }

    return (
      <a className="cashback-how" href="/cashback" target="_blank">
        <div className="cashback-how-inner">
          <p className="question">?</p>
          <p>How to earn</p>
          <img src={CashbackLogo} alt="CashbackLogo.svg" />
          <p>Cashback?</p>
        </div>
      </a>
    );
  }

  render() {
    const {
      rewardData,
      userDetails,
      constants,
      storeForm,
    } = this.props;

    return (
      <Fragment>
        <Row className="profile-container" noGutters>
          {this.renderLeftProfile()}
          <div className="profile-detail-container">
            <Col xs={12}>
              <p className="profile-header">My Account</p>
            </Col>
            <Col className="cashback-top-container" xs={12}>
              <Row noGutters>
                <Col xs={12} sm={6}>
                  {this.renderCashbackbalance()}
                </Col>
                <Col xs={12} sm={6}>
                  {this.renderCashbackInfo()}
                </Col>
              </Row>
            </Col>
            <Col xs={12}>
              <TabView
                maxTabWidthPx={128}
                tabs={[
                  'Overview',
                  'Cashback',
                ]}
                isDisableDropdown
                name="profile-tab"
              >
                {[
                  <ProfileForm
                    key="profileForm"
                    userDetails={userDetails}
                    constants={constants}
                    storeForm={storeForm}
                  />,
                  <ProfileCashback
                    key="profileCashback"
                    rewardData={rewardData}
                  />,
                ]}
              </TabView>
            </Col>
          </div>
        </Row>
        <FooterContainer
          marginTop={-50}
        />
      </Fragment>
    );
  }
}

Profile.defaultProps = {
  rewardData: null,
  validString: null,
  cashbackBalance: 0,
  storeForm: null,
  resetIndicatorInsurance: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
};

Profile.propTypes = {
  rewardData: PropTypes.array,
  validString: PropTypes.string,
  cashbackBalance: PropTypes.number,
  logOut: PropTypes.func.isRequired,
  userDetails: PropTypes.object.isRequired,
  constants: PropTypes.object.isRequired,
  storeForm: PropTypes.object,
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
};

const mapStateToProps = state => ({
  storeForm: getFormValues('profile')(state),
});

const mapDispatchToProps = dispatch => ({
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Profile));
